.content{
    margin: 40vh 30vw 0 0;
    width: 70vw;
    transition: opacity 3s;
}

.content-title{
    color: white;
    font-size: 5rem;
}

@media screen and (max-width: 1100px){
    .content{
        margin: 30vh auto 0 auto;
        width: auto;
        transition: opacity 3s;
    }

    .content-title{
        font-size: 10vw;
    }
}